@import url(https://oceanofanythingofficial.github.io/css-rgb-animation/rgb.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    color: white;
}
body{
    background-color: rgb(49, 45, 45);
}
ul{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 40px;
}
li{
    display: -webkit-flex;
    display: flex;
    border: 4px solid black;
    list-style: none;
    margin: 21px;
    padding: 30px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 30px;
    height: 114px;
    width: 228px;
    border-radius: 13px;
    color: rgb(165, 165, 165);
}
h1{
    margin-top: 5px;
    text-align: center;
    background-color: rgb(49, 45, 45);
}
h1 p{
    margin-top: 5px;
    display: inline;
    font-size: 30px;
}
h2{
    text-align: center;
}
span{
    font-size: 19px;
}
.card{
    background-color: rgb(49, 45, 45);
}
.card__name{
    text-align: center;
    padding: 6px;
}
.card__total{
    text-align: center;
}
.bg-green{
    background-color: green;
}
.bg-yellow{
background-color: rgb(138, 206, 36);
}
.bg-violate{
background-color: rgb(221, 100, 221);
}
.bg-red{
background-color: tomato;
}
.bg-blue{
background-color: steelblue;
}
.bg-brown{
background-color: sienna;
}
.bg-black{
    background-color: rgb(49, 45, 45);
}
